import React from 'react';
// import {AuthContext} from "../../../context";
import {BottomNavigation, BottomNavigationAction} from "@mui/material";
import HomeIcon from '@mui/icons-material/Home';
import AssignmentIcon from '@mui/icons-material/Assignment';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import {useNavigate} from "react-router-dom";

const MyNavbar = () => {
    // const {setIsAuth} = useContext(AuthContext);
    const [value, setValue] = React.useState(0);
    // const logout = () => {
    //     setIsAuth(false);
    //     localStorage.removeItem('auth')
    // }
    const router = useNavigate();

    return (
        <BottomNavigation
            value={value}
            onChange={(event, newValue) => {
                setValue(newValue);
            }}
            className="navbar__wrp"
        >
            <BottomNavigationAction label="Главная" icon={<HomeIcon />} onClick={() => router(`/home`)}/>
            <BottomNavigationAction label="Задания" icon={<AssignmentIcon />}  onClick={() => router(`/tasks`)}/>
            <BottomNavigationAction label="Профиль" icon={<AccountCircleIcon />} onClick={() => router(`/profile`)}/>
        </BottomNavigation>
    );
};

export default MyNavbar;