import React, {useEffect} from "react";
import {Avatar, Card } from "antd";
import {useNavigate} from "react-router-dom";
import dictImg from '../img/dict.svg'
import constructorImg from '../img/constructor.svg'
import learnImg from '../img/learn.svg'
import testsImg from '../img/test.svg'
import grammarImg from '../img/grammar.svg'
import {useDispatch} from "react-redux";
import {editHeader} from "../redux/Actions/actions";
const { Meta } = Card;

function Tasks() {
    const router = useNavigate();
    const dispatch  = useDispatch();

    useEffect(() => {
        dispatch(editHeader('Задания', false))
    }, [])

    return (
        <div className="App">
            <div >
                <Card className="task__item" onClick={() => router(`/dictionary`)}>
                    <Meta
                        avatar={<Avatar shape="square" size={64} src={dictImg} />}
                        title="Словарь"
                        description="Мои выученные слова"
                    />
                </Card>
                <Card className="task__item" onClick={() => router(`/learn`)}>
                    <Meta
                        avatar={<Avatar shape="square" size={64} src={learnImg} />}
                        title="Учим слова"
                        description="Выбери верный перевод слова"
                    />
                </Card>
                <Card className="task__item" onClick={() => router(`/constructor`)}>
                    <Meta
                        avatar={<Avatar shape="square" size={64} src={constructorImg} />}
                        title="Составь предложение"
                        description="Собери предложение из слов"
                    />
                </Card>
                <Card className="task__item">
                    <Meta
                        avatar={<Avatar shape="square" size={64} src={testsImg} />}
                        title="Тесты"
                        description="Проверь свои знания"
                    />
                </Card>
                <Card className="task__item">
                    <Meta
                        avatar={<Avatar shape="square" size={64} src={grammarImg} />}
                        title="Грамматика"
                        description="Основные правила языка"
                    />
                </Card>
            </div>
        </div>
    );
}

export default Tasks;